<template>
  <div class="layout">
    <Row>
      <Col>
        <NavBar :activeName="'experience'"></NavBar>
      </Col>
    </Row>
    <Row>
      <Col class="w1200">
        <Layout>
          <Row>
            <Breadcrumb :style="{margin: '16px 0'}">
              <BreadcrumbItem to="/"></BreadcrumbItem>
              <BreadcrumbItem></BreadcrumbItem>
            </Breadcrumb>
          </Row>
          <Row>
            <Content class="content">
              <Layout>
                <Content class="content-center">
                  <Timeline class="time-content-box">
                    <i-time-line-item :color="(item.index % 2 === 0) ? '#FFAD2C' : '#50ABF8'" v-for="item in data" :key="item.index">
                      <p class="time-title" :class="{double: (item.index % 2 !== 0)}">{{item.timeShow}}</p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}">{{item.title}}</p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}">{{item.content}}</p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}">{{item.job}}</p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}" v-for="p in item.describe" :key="p.index">{{p}}</p>
                    </i-time-line-item>
                  </Timeline>
                </Content>
              </Layout>
            </Content>
          </Row>
        </Layout>
      </Col>
    </Row>
    <Row>
      <Col>
        <Footer></Footer>
      </Col>
    </Row>
  </div>
</template>

<script>
import moment from 'moment'
import _ from 'lodash'
import NavBar from '../components/NavBar.vue' // 
import Footer from '../components/Footer.vue' // 
export default {
  name: 'Experience',
  components: {
    NavBar, // 
    Footer // 
  },
  data () {
    return {
      msg: 'Experience',
      data: [
        {
          'startTime': '2018-7-14 00:00:00',
          'endTime': '',
          'title': '',
          'content': '',
          'job': '',
          'describe': [
            '1',
            '2demo',
            '3H5Vue.js',
            '4UI',
            '5web',
            '6',
            '7bug,',
            '8'
          ]
        },
        {
          'startTime': '2017-4-17 00:00:00',
          'endTime': '2018-7-14 00:00:00',
          'title': '',
          'content': '',
          'job': '',
          'describe': [
            '1',
            '2UIWeb',
            '3H5AngularJSVue.js',
            '4UI',
            '5web',
            '6,',
            '7',
            '8bug,',
            '9',
            '10'
          ]
        },
        {
          'startTime': '2016-8-1 00:00:00',
          'endTime': '2017-4-17 00:00:00',
          'title': '',
          'content': '',
          'job': '',
          'describe': [
            '1UIWeb',
            '2H5JSjQPC',
            '3UI',
            '4',
            '5bug,'
          ]
        }
      ]
    }
  },
  methods: {
    /**
     * 
     */
    getList () {
      var self = this
      self.$loadingBar.finish()
      console.log('json')
      self.initList()
    },
    /**
     * 
     */
    initList () {
      var self = this
      self.data = _.orderBy(self.data, ['startTime'], ['desc'])
      _.each(self.data, function (v, k) {
        v.timeShow = moment(v.startTime).format('YYYY.MM') + ' - ' +
          (moment(v.endTime).format('YYYY.MM') !== 'Invalid date' ? moment(v.endTime).format('YYYY.MM') : '')
        v.index = k
      })
    },
    init () {
      var self = this
      self.getList()
    }
  },
  created () {
    var self = this
    self.init()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .layout{
    min-width: 800px;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .w1200 {
    margin: 0 auto;
    width: 1200px;
  }
  .content {
    margin-bottom: 50px;
    background: #fff;
  }
  .content-center {
    padding: 24px;
    background: #fff;
    .time-content-box {
      margin-top: 30px;
      margin-left: 50%;
      .time-title{
        position: relative;
        top: -4px;
        color: #ffffff;
        text-align: center;
        width: 140px;
        height: 25px;
        line-height: 25px;
        font-size: 14px;
        font-weight: bold;
        background: #FFAD2C;
        border-radius: 25px;
      }
      .time-title.double {
        position: relative;
        left: -175px;
        background: #50ABF8;
      }
      .time-content{
        padding-left: 5px;
        padding-top: 5px;
      }
      .time-content.double {
        position: relative;
        left: -590px;
        text-align: right;
      }
    }
  }
</style>



